.header-button {
  display: flex;
  width: 230px;
  justify-content: space-around;
  border-radius: 40px;
  padding: 10px;
  background-color: #361500;
}
.button-icon {
  width: 30px;
  color: white;
}
.button-name {
  margin-top: 5px;
  color: white;
  font-size: 18px;
  background-color: #361500;
}
.button-count {
  background-color: #8e3200;
  padding: 5px;
  border-radius: 55px;
  color: white;
  font-size: 18px;
}

.header-button:hover {
  background-color: #8e3200;
  border-color: white;
}
