.header {
  background-color: #8e3200;
  padding: 1.2rem;
}

.header-components__container {
  width: 80%;
  display: flex;
  justify-content: space-between;
  color: white;
  margin: 0 auto;
  background-color: #8e3200;
}
.header-image {
  height: 45vh;
  background-color: #8e3200;
}

.header-image img {
  width: 100%;
  height: 100%;
}

.header h1 {
  background-color: #8e3200;
}
