.meal {
  background-color: white;
  width: 95%;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  padding: 1rem;
}

.name {
  font-size: 1.4rem;
  font-weight: bold;
  background-color: white;
}

.description {
  font-style: italic;
  background-color: white;
}

.price {
  font-size: 1.2rem;
  color: #8e3200;
  font-weight: bold;
  background-color: white;
}
.label {
  font-size: 1.1rem;
  font-weight: bold;
  background-color: white;
}

.input {
  margin-left: 8px;
  width: 35px;
  background-color: white;
}
.button {
  width: 100px;
  height: 30px;
  border-radius: 15px;
  margin: 5px 0 0 15px;
  font-size: 1.1rem;
  background-color: #8e3200;
  color: white;
  border-color: white;
}

.divide {
  background-color: black;
  height: 1px;
  width: 95%;
  margin: 0 auto;
}

.style {
  background-color: white;
}
